import ResponsiveAppBar from "../containers/home/ResponsiveAppBar";
import BottomNavigator from "../containers/home/BottomNavigator";
import React, {useEffect} from "react";
import {useDispatch} from "react-redux";
import {setPageTitle} from "../features/common/headerSlice";
import {Member} from "../features/homepage/member";

const InternalPage = () => {

    const title = "Member";
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(setPageTitle({title: title}))
        document.title = title;
    }, [dispatch])

    return (
        <>
            <ResponsiveAppBar/>
            <Member/>
            <BottomNavigator/>
        </>
    )
}

export default InternalPage;
