import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {fetchAllMemberAction} from "../../../app/service/action/memberAction";
import TitleComponent from "../../../components/Title/TitleComponent";
import {
    Container,
    Pagination, Paper, styled,
    Table,
    TableBody,
    TableCell,
    tableCellClasses,
    TableContainer,
    TableHead,
    TableRow,
    TextField
} from "@mui/material";
import Loading from "../../../components/Loading/Loading";

const StyledTableCell = styled(TableCell)(({theme}) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: '#0f74f8',
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));

const StyledTableRow = styled(TableRow)(({theme}) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

export const Member = () => {
    const dispatch: any = useDispatch();
    const {isLoading, data} = useSelector(({membersState}) => membersState.members);
    const [page, setPage] = useState(1);
    const [search, setSearch] = useState();

    const handleChange = (event, value: number) => {
        setPage(value);
        // @ts-ignore
        dispatch(fetchAllMemberAction(value, search));
    };

    const handleSearchChange = (event: any) => {
        if (event.target.value.length >= 3) {
            setSearch(event.target.value);
        } else if (event.target.value === "") {
            setSearch();
        }
    };

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchAllMemberAction(page, search))
    }, [dispatch, page, search]);
    return (
        <Container>
            <TitleComponent title={'Daftar Member Pushbike Tangerang'}/>
            <div className={'search'}>
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="email"
                    label="Cari Nama Rider"
                    name="email"
                    autoComplete="email"
                    onInput={(input: any) => handleSearchChange(input)}
                />
            </div>
            {isLoading ? (
                <Loading/>
            ) : (
                <div className={'box-content'}>
                    <TableContainer component={Paper}>
                        <Table aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell>No.</StyledTableCell>
                                    <StyledTableCell>Nama Lengkap</StyledTableCell>
                                    <StyledTableCell>Kategory</StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {data.result ? data.result.map((item: any, index: number) => {
                                    return (
                                        <StyledTableRow key={index}>
                                            <TableCell>{index + 1}</TableCell>
                                            <TableCell>{item.fullname}</TableCell>
                                            <TableCell>{item.categoryRace}</TableCell>
                                        </StyledTableRow>
                                    )
                                }) : (
                                    <StyledTableRow>
                                        <TableCell>Data tidak ditemukan!!!</TableCell>
                                    </StyledTableRow>
                                )}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
            )}
            <div className={'event-pagination'}>
                <Pagination count={data.result ? data.metaData.totalPage : 0}
                            page={page}
                            color={'primary'}
                            onChange={handleChange}/>
            </div>
        </Container>
    )
}
